.App {
  text-align: center;
}

form {
  margin-bottom: 20px;
}

input {
  padding: 10px;
  margin-right: 10px;
}

button {
  padding: 10px;
}

.issue {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.issue1 {
  background-color: gray;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.issue input {
  width: 400px;
  height: 40px;
}

.issue button {
  border: none;
  color: white;
  background-color: #1d29cf;
  cursor: pointer;
  border-radius: 5px;
}

.issue button:hover {
  background-color: #141fb4;
}

.issue select {
  width: 320px;
  height: 50px;
}

.issue-list {
  padding: 10px;
  width: 700px;
  margin: 0 auto;
  border-radius: 5px;
}

.issue-item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 10px;
  align-items: flex-start;
  margin: 10px 0;
  padding: 20px;
  background-color: gray;
  border-radius: 5px;
  width: 100%;
}

.issue-details {
  font-weight: bold;
}

.issue-text {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
  resize: none;
  height: 60px;
}

.issue-text1 {
  width: 100%;
}

.delete-button {
  padding: 5px 10px;
  background-color: #1d29cf;
  border: none;
  color: white;
  border-radius: 3px;
  cursor: pointer;
}

.delete-button:hover {
  background-color: #050c69;
}

.error-message {
  color: red;
  font-weight: bold;
  margin: -10px 0px;
}
